import React, { Component } from 'react'

class PerfilPage extends Component {
    state = {
        login: ''
    }
    componentDidMount() {
        this.login = this.props.match.params.login
    }
    render() {
        return (
            <div>
                { this.state.login }
            </div>
        )
    }
}

export default PerfilPage


